.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgb(82, 115, 230);
    display: flex;
    align-items: center;
}
.header__img{
    margin-left: 20px;
}
.header__nav{
display: flex;
width: 100%;
margin-left: 30px;

}

.nav__ul{
    
    padding: 0;
    display: flex;
    width: 300px;
    justify-content: space-between;
    position: relative;
}

.ul__list{
list-style: none;
}

.ul__list a {
    color: #fff;
    font-weight: bold;
    text-decoration: none;
}
.ul__list a.active{
    color: rgb(223, 223, 46);
}


.hard__words__count{

    position: absolute;
  right: -20px;
  background-color: rgba(12, 123, 143, 0.8);
  width: 20px;
  height: 20px;
  border-radius: 100%;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 10px;
  font-size: 13px;
   
}